import { stopSubmit } from 'redux-form'
import { authAPI } from '../api/api'

const SET_USER_DATA = 'SET_USER_DATA'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'
const SET_PHOTO = 'SET_PHOTO'
let initialState = {
  id: null,
  email: null,
  login: null,
  isFetching: false,
  isAuth: false,
  photo: null,
}
const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.payload,
        isAuth: action.payload.isAuth,
      }
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      }
    case SET_PHOTO:
      return {
        ...state,
        photo: action.photo,
      }
    default:
      return state
  }
}

export const setAuthUserData = (id, login, email, isAuth) => ({
  type: SET_USER_DATA,
  payload: { id, login, email, isAuth },
})
export const toggleIsFetching = (isFetching) => ({
  type: TOGGLE_IS_FETCHING,
  isFetching,
})
export const setPhoto = (url) => ({
  type: SET_PHOTO,
  photo: url,
})
export const authent = () => (dispatch) => {
  dispatch(toggleIsFetching(true))
  return authAPI.me().then((data) => {
    dispatch(toggleIsFetching(false))
    if (data.resultCode === 0) {
      let { id, login, email } = data.data
      dispatch(setAuthUserData(id, login, email, true))
      authAPI.getProfilePhoto(id).then((response) => {
        if (response.photos.small === null) {
          dispatch(
            setPhoto(
              'https://media.istockphoto.com/photos/businessman-silhouette-as-avatar-or-default-profile-picture-picture-id476085198?k=20&m=476085198&s=612x612&w=0&h=8J3VgOZab_OiYoIuZfiMIvucFYB8vWYlKnSjKuKeYQM='
            )
          )
        } else dispatch(setPhoto(response.photos.small))
      })
    }
  })
}

export const login = (email, password, rememberMe) => (dispatch) => {
  authAPI.login(email, password, rememberMe).then((data) => {
    if (data.resultCode === 0) {
      dispatch(authent())
    } else {
      let message = data.messages.length > 0 ? data.messages[0] : 'Some Error'
      dispatch(stopSubmit('login', { _error: message }))
    }
  })
}
export const logout = () => {
  return (dispatch) => {
    authAPI.logout().then((data) => {
      if (data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false))
      }
    })
  }
}
export default authReducer
