const SEND_MESSAGE = 'SEND-MESSAGE'

let initialState = {
  dialogs: [
    { id: 1, name: 'John' },
    { id: 2, name: 'Ernest' },
    { id: 3, name: 'Calvin' },
    { id: 4, name: 'Martin' },
    { id: 5, name: 'Franklin' },
    { id: 6, name: 'Michael' },
  ],
  messages: [
    { id: 1, message: 'Hi' },
    { id: 2, message: 'Hello' },
    { id: 3, message: 'How are you?' },
    { id: 4, message: 'Im okay, are you?' },
    { id: 5, message: 'Goood' },
    { id: 6, message: 'Nice' },
  ],
}
//state - отдельная часть полного стейта, в данном случае придет dialogsPage, поэтому обращаемся через state сразу к messages и тд
const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case SEND_MESSAGE: {
      return {
        ...state,
        messages: [
          ...state.messages,
          {
            id: 7,
            message: action.newMessageBody,
          },
        ],
      }
    }
    default:
      return state
  }
}
export const sendMessageActionCreator = (newMessageBody) => ({
  type: SEND_MESSAGE,
  newMessageBody,
})
export default dialogsReducer
