import React from 'react'
import { connect } from 'react-redux'
import {
  follow,
  setCurrentPage,
  unfollow,
  toggleFollowingInProgress,
  requestUsers,
} from '../../redux/users-reducer'
import {
  followingInProgress,
  getCurrentPage,
  getPageSize,
  getTotalUsersCount,
  getUsers,
  isFetching,
} from '../../redux/users-selectors'
import Preloader from '../common/Preloader/Preloader'
import Users from './Users'
class UsersContainer extends React.Component {
  componentDidMount() {
    const { currentPage, pageSize } = this.props
    this.props.requestUsers(currentPage, pageSize)
    /*
    this.props.toggleIsFetching(true)
    usersAPI
      .getUsers(this.props.currentPage, this.props.pageSize)
      .then((data) => {
        this.props.toggleIsFetching(false)
        this.props.setUsers(data.items)
        this.props.setTotalUsersCount(data.totalCount)
      })
      */
  }

  onPageChanged = (pageNumber) => {
    const { pageSize } = this.props
    this.props.setCurrentPage(pageNumber)
    this.props.requestUsers(pageNumber, pageSize)
  }

  render() {
    return (
      <>
        {this.props.isFetching ? <Preloader /> : null}
        <Users
          totalUsersCount={this.props.totalUsersCount}
          pageSize={this.props.pageSize}
          currentPage={this.props.currentPage}
          onPageChanged={this.onPageChanged}
          unfollow={this.props.unfollow}
          follow={this.props.follow}
          users={this.props.users}
          followingInProgress={this.props.followingInProgress}
        />
      </>
    )
  }
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: isFetching(state),
    followingInProgress: followingInProgress(state),
  }
}
/*
let mapDispatchToProps = (dispatch) => {
  return {
    follow: (userId) => {
      dispatch(followAC(userId))
    },
    unfollow: (userId) => {
      dispatch(unfollowAC(userId))
    },
    setUsers: (users) => {
      dispatch(setUsersAC(users))
    },
    setCurrentPage: (pageNumber) => {
      dispatch(setCurrentPageAC(pageNumber))
    },
    setTotalUsersCount: (totalCount) => {
      dispatch(setTotalUsersCountAC(totalCount))
    },
    toggleIsFetching: (isFetching) => {
      dispatch(toggleIsFetchingAC(isFetching))
    },
  }
}
*/

export default connect(mapStateToProps, {
  follow,
  unfollow,
  setCurrentPage,
  toggleFollowingInProgress,
  requestUsers,
})(UsersContainer)
//connect прокинет нужные части state'a и нужные callback'и в пропсы UsersContainer'а
//connect понимает, что ему пришел объект и делает все за нас, прокидывает нужные параметры в actioncreature'ы и диспатчит
