import { profileAPI, usersAPI } from '../api/api'

const ADD_POST = 'ADD-POST'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'
const DELETE_POST = 'DELETE_POST'

let initialState = {
  posts: [
    { id: 1, message: 'Hi, how are you?', likesCount: 19 },
    { id: 2, message: "It's my first post", likesCount: 20 },
    { id: 3, message: 'Facebook is dead?', likesCount: -129 },
    { id: 4, message: 'Yes', likesCount: 200 },
  ],
  profile: null,
  status: '',
}

//state - отдельная часть полного стейта, в данном случае придет profilePage, поэтому обращаемся через state сразу к posts и тд
const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      return {
        ...state,
        posts: [
          ...state.posts,
          { id: 5, message: action.newPostText, likesCount: 0 },
        ],
      }
    }
    case DELETE_POST: {
      return {
        ...state,
        posts: state.posts.filter((p) => p.id != action.postId),
      }
    }
    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      }
    }
    case SET_STATUS: {
      return {
        ...state,
        status: action.status,
      }
    }
    default:
      return state
  }
}
export const addPostActionCreator = (newPostText) => {
  return {
    type: ADD_POST,
    newPostText,
  }
}
export const deletePost = (postId) => {
  return {
    type: DELETE_POST,
    postId,
  }
}
export const setStatus = (status) => ({
  type: SET_STATUS,
  status,
})
export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
})
export const setProfile = (userId) => {
  return async (dispatch) => {
    let response = await profileAPI.getProfile(userId)
    dispatch(setUserProfile(response))
  }
}
export const getStatus = (userId) => {
  return (dispatch) => {
    profileAPI.getStatus(userId).then((response) => {
      dispatch(setStatus(response.data))
    })
  }
}
export const updateStatus = (status) => {
  return (dispatch) => {
    profileAPI.updateStatus(status).then((response) => {
      if (response.data.resultCode === 0) dispatch(setStatus(status))
    })
  }
}
export default profileReducer
