import React from 'react'
import style from './Friends.module.css'
const Friends = (props) => {
  let friends = props.friendList.map((el) => {
    return (
      <div className={style.friendItem} key={el.id}>
        <img src={el.img} />
        <div>{el.name}</div>
      </div>
    )
  })
  return (
    <div>
      Friends
      <div className={style.friendList}>{friends}</div>
    </div>
  )
}
export default Friends
