import './App.css'
import Navbar from './components/Navbar/Navbar'
import News from './components/News/News'
import Music from './components/Music/Music'
import Settings from './components/Settings/Settings'
import React, { Suspense } from 'react'
import { connect, Provider } from 'react-redux'
import { BrowserRouter, Route } from 'react-router-dom'
import UsersContainer from './components/Users/UsersContainer'
import HeaderContainer from './components/Header/HeaderContainer'
import Login from './components/Login/Login'
import { initializeApp } from './redux/app-reducer'
import { compose } from 'redux'
import { withRouter } from 'react-router'
import Preloader from './components/common/Preloader/Preloader'
import store from './redux/redux-store'
const DialogsContainer = React.lazy(() =>
  import('./components/Dialogs/DialogsContainer.jsx')
)
const ProfileContainer = React.lazy(() =>
  import('./components/Profile/ProfileContainer.jsx')
)
class App extends React.Component {
  componentDidMount() {
    this.props.initializeApp()
  }
  render() {
    console.log(this.props.initialiazed)
    if (!this.props.initialiazed) {
      return <Preloader />
    } else {
      return (
        <div className="app-wrapper">
          <HeaderContainer />
          <Navbar friendList={this.props.state.friendList} />
          <div className="app-wrapper-content">
            <Suspense fallback={<div>Loading...</div>}>
              <Route
                path={this.props.state.sidebar.path[0] + '/:userId?'} //через двоеточие указываются параметры для withRouter
                render={() => <ProfileContainer /*store={props.store}*/ />}
              />
              <Route
                path={this.props.state.sidebar.path[1]}
                render={() => {
                  return <DialogsContainer /*store={props.store}*/ />
                }}
              />
              <Route
                path={this.props.state.sidebar.path[2]}
                render={() => <News />}
              />
              <Route
                path={this.props.state.sidebar.path[3]}
                render={() => <Music />}
              />
              <Route
                path={this.props.state.sidebar.path[4]}
                render={() => <Settings />}
              />
              <Route path={'/users'} render={() => <UsersContainer />} />
              <Route path={'/login'} render={() => <Login />}></Route>
            </Suspense>
          </div>
        </div>
      )
    }
  }
}
const mapStateToProps = (state) => {
  return {
    initialiazed: state.app.initialiazed,
  }
}
let AppContainer = compose(
  withRouter,
  connect(mapStateToProps, {
    initializeApp,
  })
)(App)
const MainApp = () => {
  return (
    <BrowserRouter>
      <Provider store={store}>
        <AppContainer state={store.getState()} />
      </Provider>
    </BrowserRouter>
  )
}
export default MainApp
